let layer = 0;

const cssToTailWind = (key: string, value: string): string | undefined => {
    switch (key) {
        case 'background-color':
            if (value.startsWith('--use-theme:')) {
                value = value.replace('--use-theme:', '');
                return `bg-${value}`;
            } else if (value.startsWith('#')) {
                return `bg-${value.replace('#', '')}`
            }
            break;

        case 'border-radius':
            if (value.startsWith('--use-theme:')) {
                value = value.replace('--use-theme:', '');
                return `rounded-${value}`;
            }
            break;

        case 'color':
            if (value.startsWith('--use-theme:')) {
                value = value.replace('--use-theme:', '');
                return `text-${value}`;
            }
            break;

        default:
            break;
    }
}

const text = (base: ElementBase, uuid: number, options?: any): ElementBaseMarkup => {

    return {

        markup: `const LayerStyled_${uuid} = styled('${base.layer}')<ElementStyledProps>(
        baseStyles_${uuid},
        stateStyles_${uuid},
        errorStyles_${uuid}
        )`,
        elementOpen: `<LayerStyled_${uuid} class="${options.cssClass}" {...props} {...partialState}>`,
        elementClose: `</LayerStyled_${uuid}>`,
        elementContent: `{props.${base.characters.path} || '${base.characters.default}'}`,
    };

}

const icon = (base: ElementBase, uuid: number, options?: any): ElementBaseMarkup => {
    return {

        markup: `const LayerStyled_${uuid} = styled('${base.layer}')<ElementStyledProps>(
            baseStyles_${uuid},
            stateStyles_${uuid},
            errorStyles_${uuid}    
        )`,
        elementOpen: `<LayerStyled_${uuid} class="${options?.cssClass}" {...props} {...partialState}>`,
        elementClose: `</LayerStyled_${uuid}>`,
        elementContent: `{props.${base.icon.path} || ${base.icon.default?base.icon.default:''} }`,
    };

}

const block = (base: ElementBase, uuid: number, options?: any): ElementBaseMarkup => {

    return {

        markup: `        
        const LayerStyled_${uuid} = styled('${base.layer}')<ElementStyledProps>(
            baseStyles_${uuid},
            stateStyles_${uuid},
            errorStyles_${uuid}
        )`,

        elementOpen: `<LayerStyled_${uuid} class="${options?.cssClass}"  {...props} {...partialState}>`,
        elementClose: `</LayerStyled_${uuid}>`
    };

}

const drawComponent = (node: ElementBase, uuid: number, options?:any): ElementBaseMarkup => {

    switch (node.type) {
        case 'TEXT':
            return text(node, uuid, options);

        case 'BLOCK':
            return block(node, uuid, options);

          case 'ICON':
            return icon(node, uuid, options);

        default:
            return { markup: '', elementOpen: '', elementClose: '' };
    }
}


const styleDiff = (styles: any, stateStyles?: any[]): any[] | undefined => {
    var newStateStyles: any = [];

    if (!stateStyles) {
        return;
    }

    for (const key in styles) {
        const element = styles[key];
        for (const iterator of stateStyles) {
            if (String(iterator.styles[key]).trim().toLowerCase() === String(element).trim().toLowerCase()) {
                delete iterator.styles[key];
            }
        }
    }
    return stateStyles;
}

const styleConverter = (styles: any, stateStyles?: any[]): {base:string,state?:ElementState[],errors:string[]} => {

    let css = ``;
    stateStyles = styleDiff(styles, stateStyles);

    let stateTW = '';

    const settings = {
        autoConvertColor: true,
        autoConvertSpacing: true,
        remConversion: 16
    };
    const tailWindStyles: any = [];
    const errors: any = [];

    let finalCSS: any = {};


    for (const key in styles) {

        let styleKey = key;
        styleKey = styleKey.split(/(?=[A-Z])/).join(' ').replace(' ', '-').toLowerCase();
        let styleValue = String(styles[key]).toLowerCase().trim();
        
        var ctow = cssToTailWind(styleKey, styleValue);

        if (ctow)
            tailWindStyles.push(ctow);
        else
            convertCss(styleKey, styleValue, tailWindStyles, errors, settings);
    }

    if (stateStyles) {
        for (let state of stateStyles) {
            state.tailwind = [];
            state.tailwindError = [];

            for (const _key in state.styles) {

                let styleKey = _key;
                styleKey = styleKey.split(/(?=[A-Z])/).join(' ').replace(' ', '-').toLowerCase();
                let styleValue = String(state.styles[_key]).toLowerCase().trim();

                var ctow = cssToTailWind(styleKey, styleValue);
                if (ctow)
                    state.tailwind.push(ctow);
                else
                    convertCss(styleKey, styleValue, state.tailwind, state.tailwindError, settings);
            }

            state.tailwind = state.tailwind.filter((style: string) => !tailWindStyles.find((twStyle: string) => twStyle == style));
            state.tailwindString = '';
            // console.log('TW-State-Value', `group-hover:hover:${tailWindStyles.join(' group-hover:hover:')}`);
            switch (state.event) {
                case 'hover':
                    state.tailwindString += state.tailwind.length > 0 ? ` group-hover:${state.tailwind.join(' group-hover:')}` : '';
                    state.tailwindString += state.tailwind.length > 0 ? ` hover:${state.tailwind.join(' hover:')}` : '';
                    break;
                case 'active':
                    state.tailwindString += state.tailwind.length > 0 ? ` active:${state.tailwind.join(' active:')}` : '';
                    break;
                case 'focus':
                    state.tailwindString += state.tailwind.length > 0 ? ` group-focus:${state.tailwind.join(' group-focus:')}` : '';
                    state.tailwindString += state.tailwind.length > 0 ? ` focus:${state.tailwind.join(' focus:')}` : '';
                    break;
                default:
                    state.tailwindString += state.tailwind.length > 0 ? `${state.tailwind.join(' ')}` : '';
                    break;
            }

        }

    }


    console.log('TW-Value', tailWindStyles.join(' '));
    console.log('TW-Errors', errors);
    // console.log('css', css);
    return {base: tailWindStyles.join(' '), state:stateStyles, errors:errors };
}

const generateBaseMarkup = (base: ElementBase, parentUUID?: number, siblingOrder?: number | null | undefined, states?: ElementState | ElementState[] | null | undefined): any => {
    var styles = styleConverter(base.styles, states ? states.map((state: ElementState) => { return { styles: state.styles, event: state.event, path:state.path, allowed:state.allowed, disallowed:state.disallowed, name:state.name } }) : undefined);

    let markup: any[] = [];
    let nodeOrder = 0;
    let path = '';
    let defaultCss = '';
    var uuid = layer++;

    if (parentUUID) {
        path = `${parentUUID}_${uuid}`;
    } else {
        path = `${uuid}`;
        defaultCss = 'group '
    }


    if (siblingOrder != null || siblingOrder != undefined) {
        nodeOrder = siblingOrder + 1;
    }

    if (base.children) {
        let previousChildOrder = null;
        var index = 0;
        for (let iterator of base.children) {
            iterator = generateBaseMarkup(iterator, uuid, previousChildOrder, states ? states.map((state: ElementState) => state.children ? state.children[index] : undefined) : undefined);
            index++;
            previousChildOrder = iterator.order;
        }
    }




    `const stateStyles = ({ disabled }: ElementStyledProps) =>[
        disabled && tw\`bg-f5f2f2ff\`,
        !disabled && tw\`hover:bg-66c2ffff\`,
        !disabled && tw\`active:bg-005c99ff\`
    ]`;

    `
    const errorStyles_${uuid} = () => ({
        ${styles.errors && styles.errors.length>0?styles.errors.map(err => {let key:any = err.split('-'); if(key[1]) key[1]= key[1].charAt(0).toUpperCase() + key[1].slice(1); key = key.join('').replace(';',''); key = key.split(':');  return `${key[0]} : '${key[1].trim()}'`  }).join(',\n'):''}

    });
    `

    let stateTw = ``;
    let errorStateTw = ``;
    let stateTwString = ``;
    var stateMap:any = {};

    if(styles.state){
       stateTw = `const stateStyles_${uuid} = ({ ${styles.state?.map(state=>state.name).join(' , ')} }: ElementStyledProps) =>[`;
        let removeLastCharacter = false;
        for (const state of styles.state) {
            if(!stateMap['props.'+state.name]){
                stateMap['props.'+state.name] = [true];
            }

            if(state.allowed){
                for (const iterator of state.allowed) {
                    if(!stateMap['props.'+iterator]){
                        stateMap['props.'+iterator] = [true, `${'props.'+state.name}`];
                    }else{
                        stateMap['props.'+iterator].push('props.'+state.name);
                    }       
                }
            }

            if(state.disallowed){
                for (const iterator of state.disallowed) {
                    if(!stateMap['props.'+iterator]){
                        stateMap['props.'+iterator] = [true, `!${'props.'+state.name}`];
                    }else{
                        stateMap['props.'+iterator].push('!'+'props.'+state.name);
                    }       
                }
            }

            if(state.tailwindString){
                removeLastCharacter = true;

                stateTw += `
                    ${state.name} && tw\`${state.tailwindString}\`,
                `
                stateTwString = state.tailwindString;

            }

            if(state.tailwindError.length > 0){
                errorStateTw += ` ${state.tailwindError.map((err: string) => {let key:any = err.split('-'); if(key[1]) key[1]= key[1].charAt(0).toUpperCase() + key[1].slice(1); key = key.join('').replace(';',''); key = key.split(':');  return `${key[0]} : ${state.name} && '${key[1].trim()}'`  }).join(',\n')},
                
                `
            }
           
        }

        if(removeLastCharacter){
            stateTw = stateTw.substring(0, stateTw.length - 1); //remove last unused comma
        }

        stateTw += ']'
    }


    var component = drawComponent(base, uuid, {
        cssClass:defaultCss, stateTw , stateTwString, errors:styles.errors
    });


    base.markup = `
    
    /***********************************************LAYER-${uuid}*****************************************/
    const baseStyles_${uuid} = tw\`${String(styles.base).includes('outline') ? '' : 'outline-none'} ${String(styles.base).trim()}\`
    
    const errorStyles_${uuid} = (${styles.state?.map(state=>state.name).join(' , ')}) => ({
        ${styles.errors && styles.errors.length>0?styles.errors.map(err => {let key:any = err.split('-'); if(key[1]) key[1]= key[1].charAt(0).toUpperCase() + key[1].slice(1); key = key.join('').replace(';',''); key = key.split(':');  return `${key[0]} : '${key[1].trim()}'`  }).join(',\n')+',':''}
        ${errorStateTw}
    });

    ${stateTw}
    ${component.markup}
    /***********************************************LAYER-${uuid}*****************************************/

   `;

   base.stateMap = stateMap;

    base.elementOpen = component.elementOpen;
    base.elementClose = component.elementClose;
    base.elementClose = component.elementClose;
    base.elementContent = component.elementContent;
    base.order = nodeOrder;
    base.path = path;


    return base;


    //+ (markup.length > 0) ? markup.join(' '): '';

}



const getElementMarkup = (base: ElementBase): any => {
    return `${base.markup}
        ${base.children ? base.children.map((childBase: ElementBase) => {
        return getElementMarkup(childBase);
    }).join(' ') : ''}`;
}


const getElementStructureMarkup = (base: ElementBase): any => {
    return `${base.elementOpen}
        ${base.elementContent ? base.elementContent : ''}
        ${base.children ? base.children.map((childBase: ElementBase) => {
        return getElementStructureMarkup(childBase);
    }).join(' ') : ''}
        ${base.elementClose}`;
}

const sortStates = (states: ElementState[]): ElementState[] => {
    const sortOrder = ['hover', 'focus', 'active'];
    return states.sort((stateA: ElementState, stateB: ElementState) => {
        let a: any = sortOrder.length+1;
        let b: any = sortOrder.length+1;

        if (stateA.event)
            a = sortOrder.indexOf(stateA.event);

        if (stateB.event)
            b = sortOrder.indexOf(stateB.event);

        return a - b;
    });

}

const visitNode = (node: Element) => {
    for (let variant of node.variants) {
        layer = 0 ;

        variant.states = sortStates(variant.states);
        variant.base = generateBaseMarkup(variant.base, undefined, undefined, variant.states);
        let stateCheckString = ``;

        const stateProps = [];
        const defaultsProps = [];

        for (const key in variant.base.stateMap) {
                const element = variant.base.stateMap[key];
                stateCheckString += `
                    ${key.replace('props.','partialState.')} = ${element.join(' && ')} && ${key};
                `;
                stateProps.push(key.replace('props.','')+'?: boolean');
                defaultsProps.push(key.replace('props.','')+': false');
        }

        stateProps.join(', \n')

        // stateProps[stateProps.length - 1] = stateProps[stateProps.length - 1]

        console.log(`


            import { styled } from 'emotion-solid'
            import tw from 'twin.macro'
            import withDefaults from '../../utils/with-defaults'
            import type { Component, JSX } from 'solid-js'



            export type ElementStyledProps = {
                [key:string]:any,
                type?: any,
                border?: string,
                color?: any,
                shape?: any,
                shadow?: boolean,
                imagePath?: string,
                text?: string,
                ${stateProps.join(', \n')}
            }

            ${getElementMarkup(variant.base)}

            const ButtonElement: Component<ElementStyledProps> = (props) => {

                let partialState: any = {};
              
                ${stateCheckString}

                return (
                    ${getElementStructureMarkup(variant.base)}
                )
            };

            export const Button = withDefaults(ButtonElement, {
                type: 'filled',
                shape: 'square',
                color: 'primary',
                characters: undefined,
                ${defaultsProps.join(', \n')}
            });



        `)
    }
}

visitNode(element);
