import { color, fill, Node,Effect } from "./types";
import { optimize } from 'svgo';

const VECTOR_TYPES = ['VECTOR', 'LINE', 'REGULAR_POLYGON', 'ELLIPSE'];
const GROUP_TYPES = ['GROUP', 'BOOLEAN_OPERATION'];
const SPACE_STYLE_KEYS = ['paddingLeft', 'paddingRight', 'paddingBottom', 'paddingTop', 'marginLeft', 'marginRight', 'marginBottom', 'marginTop'];
const borderKey = 'cornerRadius';
let zIndex = 0;

function rgba2hex(r: number, g: number, b: number, a: number) {
    if (r > 255 || g > 255 || b > 255 || a > 255)
        throw "Invalid color component";

    r = Math.round(r * 255);
    g = Math.round(g * 255);
    b = Math.round(b * 255);
    a = Math.round(a * 255);

    return ("0" + parseInt(r.toString(), 10).toString(16)).slice(-2) +
        ("0" + parseInt(g.toString(), 10).toString(16)).slice(-2) +
        ("0" + parseInt(b.toString(), 10).toString(16)).slice(-2) +
        ("0" + parseInt(a.toString(), 10).toString(16)).slice(-2);
}

function rgb2hex(r: number, g: number, b: number) {
    if (r > 255 || g > 255 || b > 255)
        throw "Invalid color component";
    return ((r << 16) | (g << 8) | b).toString(16);
}


function rgbaToHex(color: color) {
    if (color.a === undefined) {
        return '#' + rgb2hex(color.r, color.g, color.b);
    }
    return '#' + rgba2hex(color.r, color.g, color.b, color.a);
}

function colorString(color: { r: number; g: number; b: number; a: any; }) {
    try {
        if (color)
            return `rgba(${Math.round(color.r * 255)}, ${Math.round(color.g * 255)}, ${Math.round(color.b * 255)}, ${color.a})`;
    } catch (e) {
        debugger;
    }

}

function dropShadow(effect: { offset: { x: any; y: any; }; radius: any; color: any; }) {
    try {
        return `${effect.offset.x}px ${effect.offset.y}px ${effect.radius}px ${colorString(effect.color)}`;
    } catch (e) {
        debugger;
    }
}

function innerShadow(effect: { offset: { x: any; y: any; }; radius: any; color: any; }) {
    try {
        return `inset ${effect.offset.x}px ${effect.offset.y}px ${effect.radius}px ${colorString(effect.color)}`;
    } catch (e) {
        debugger;
    }
}

function nodeSort(a: { absoluteBoundingBox: { y: number; }; }, b: { absoluteBoundingBox: { y: number; }; }) {
    if (a.absoluteBoundingBox.y < b.absoluteBoundingBox.y) return -1;
    else if (a.absoluteBoundingBox.y === b.absoluteBoundingBox.y) return 0;
    else return 1;
}

function imageURL(hash: any) {
    const squash = hash.split('-').join('');
    return `url(https://s3-us-west-2.amazonaws.com/figma-alpha/img/${squash.substring(0, 4)}/${squash.substring(4, 8)}/${squash.substring(8)})`;
}

function backgroundSize(scaleMode: any) {
    if (scaleMode === 'FILL') {
        return 'cover';
    }
}

function fillColor(fills: fill[]) {

    let backgroundColor: string = '';
    let blendMode: string = '';
    let backgroundImage: string = '';
    let hex = '';

    for (const fill of fills) {
        if (fill.visible == false)
            continue;
        if (fill.type === 'SOLID') {
            backgroundColor += colorString(fill.color) + ' ';
            hex = rgbaToHex(fill.color);
        }else if(fill.type === 'IMAGE'){
            backgroundImage = `url( ${imageURL(fill.imageRef)})`
        }

        if (fill.blendMode) {
            switch (fill.blendMode) {
                case 'NORMAL':
                    blendMode += 'normal '
                    break;
                case 'NORMAL':
                    blendMode += 'normal '
                    break;

                default:
                    var mode = fill.blendMode;
                    mode = mode.replace('_', '-');
                    mode = mode.toLowerCase();
                    blendMode += `${mode} `
                    break;
            }
        }

    }

    return { backgroundColor, blendMode, backgroundImage, hex };
}

function fillEffects(fills: Effect[]) {

    let filter: string = '';
    let blendMode: string = '';
    let backgroundImage: string = '';
    let hex = '';

    for (const effect of fills) {
        if (effect.visible == false)
            continue;
        if (effect.type === 'DROP_SHADOW') {
            filter += `drop-shadow(${effect.offset?.x}px ${effect.offset?.y}px ${effect.radius}px ${colorString(effect.color)})`;
            hex = rgbaToHex(effect.color);
        }

        if (effect.blendMode) {
            switch (effect.blendMode) {
                case 'NORMAL':
                    blendMode += 'normal '
                    break;
                case 'NORMAL':
                    blendMode += 'normal '
                    break;

                default:
                    var mode = effect.blendMode;
                    mode = mode.replace('_', '-');
                    mode = mode.toLowerCase();
                    blendMode += `${mode} `
                    break;
            }
        }

    }

    return { filter, blendMode, hex };
}

function getPaint(paintList: string | any[]) {
    if (paintList && paintList.length > 0) {
        return paintList[paintList.length - 1];
    }

    return null;
}

function paintToLinearGradient(paint: any) {
    const handles = paint.gradientHandlePositions;
    const handle0 = handles[0];
    const handle1 = handles[1];

    const ydiff = handle1.y - handle0.y;
    const xdiff = handle0.x - handle1.x;

    const angle = Math.atan2(-xdiff, -ydiff);
    const stops = paint.gradientStops.map((stop: any) => {
        return `${colorString(stop.color)} ${Math.round(stop.position * 100)}%`;
    }).join(', ');
    return `linear-gradient(${angle}rad, ${stops})`;
}

function paintToRadialGradient(paint: any) {
    const stops = paint.gradientStops.map((stop: any) => {
        return `${colorString(stop.color)} ${Math.round(stop.position * 60)}%`;
    }).join(', ');

    return `radial-gradient(${stops})`;
}

function expandChildren(node: Node, parent: Node | null, minChildren: Node[], maxChildren: Node[], centerChildren: Node[], offset: number) {
    const children = node.children;
    let added = offset;

    if (children) {
        for (let i = 0; i < children.length; i++) {
            const child = children[i];

            if (parent != null && (node.type === 'COMPONENT' || node.type === 'INSTANCE')) {
                child.constraints = { vertical: 'TOP_BOTTOM', horizontal: 'LEFT_RIGHT' };
            }

            if (GROUP_TYPES.indexOf(child.type) >= 0) {
                added += expandChildren(child, parent, minChildren, maxChildren, centerChildren, added + i);
                continue;
            }

            child.order = i + added;

            if (child.constraints && child.constraints.vertical === 'BOTTOM') {
                maxChildren.push(child);
            } else if (child.constraints && child.constraints.vertical === 'TOP') {
                minChildren.push(child);
            } else {
                centerChildren.push(child);
            }
        }

        minChildren.sort(nodeSort);
        maxChildren.sort(nodeSort);

        return added + children.length - offset;
    }

    return added - offset;
}

const fetchSVG = async (node: any) => {
    var svg: any;
    svg = await fetch(`https://api.figma.com/v1/images/eVe5oLECdlbVqpPNQpHWMZ?ids=${node.id}&format=svg`, {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json',
            'X-Figma-Token': '208305-046359c8-502e-4530-939c-6595ad08de46'
        }
    });

    svg = await svg.json();
    const urlOfSVG = svg.images[node.id];
    if (urlOfSVG) {
        svg = await fetch(urlOfSVG);
        svg = await svg.text();
        svg = svg.replace('<svg ', '<svg preserveAspectRatio="none" ');
        node.svg = svg;
    }
    return node;
}

export const attachSVG = async (node: any) => {
    var allVectors = !(node.children.filter((child: { type: any }) => child.type && child.type !== 'VECTOR').length);
    if (node.type === 'FRAME' && allVectors) {
        return fetchSVG(node);
    }
    else if (node.type === 'VECTOR') {
        return fetchSVG(node);
    }
    else if (node.children) {
        for (let iterator of node.children) {
            iterator = await attachSVG(iterator)
        }
    }
    return node;
}

const positionCalculator = (node:Node, bounds: any, styles:any,cHorizontal: string,cVertical: string):any =>{
    if (cHorizontal === 'LEFT_RIGHT') {
        if (bounds != null) {
            styles.marginLeft = bounds.left;
            styles.marginRight = bounds.right;
            styles.flexGrow = 1;
        }
    } else if (cHorizontal === 'RIGHT') {
        styles.justifyContent = 'flex-end';
        if (bounds != null) {
            styles.marginRight = bounds.right;
            styles.width = bounds.width + 'px';
            styles.minWidth = bounds.width + 'px';
        }
    } else if (cHorizontal === 'CENTER') {
        styles.justifyContent = 'center';
        if (bounds != null) {
            styles.width = bounds.width + 'px';
            styles.marginLeft = bounds.left && bounds.right ? bounds.left - bounds.right : null;
        }
    } else if (cHorizontal === 'SCALE') {
        if (bounds != null && bounds.left != undefined && bounds.right != undefined) {
            const parentWidth = bounds.left + bounds.width + bounds.right;
            styles.width = bounds.width+'px' || `${(bounds.width * 100 / parentWidth)*parentWidth}px`;
            if(node.type === 'RECTANGLE'){
                debugger;
            }                
            
            if (Number.isInteger(bounds.left))
                styles.marginLeft = bounds.left+'px' || `${(bounds.left * 100 / parentWidth)*parentWidth}px`;
        }
    } else {
        if (bounds != null) {
            styles.marginLeft = bounds.left;
            styles.width = bounds.width + 'px';
            styles.minWidth = bounds.width + 'px';
        }
    }

    if (bounds && bounds.height && cVertical !== 'TOP_BOTTOM') styles.height = bounds.height;
    if (cVertical === 'TOP_BOTTOM') {

        if (bounds != null) {
            styles.marginTop = bounds.top;
            styles.marginBottom = bounds.bottom;
        }
    } else if (cVertical === 'CENTER') {

        styles.alignItems = 'center';
        if (bounds != null) {
            if (bounds.top)
                if (bounds.bottom)
                    styles.marginTop = bounds.top - bounds.bottom;
                else
                    styles.marginTop = bounds.top;
        }
    } else if (cVertical === 'SCALE') {

        if (bounds != null && bounds.top != undefined && bounds.bottom != undefined) {
            const parentHeight = bounds.top + bounds.height + bounds.bottom;
            styles.height = bounds.height+'px' || `${(bounds.height * 100 / parentHeight)*parentHeight}px`;
            styles.top = bounds.top +'px' || `${((bounds.top || 0) * 100 / parentHeight)*parentHeight}px`;
        }
    } else {
        if (bounds != null) {
            styles.marginTop = bounds.top;
            styles.marginBottom = bounds.bottom;
            styles.minHeight = styles.height + 'px';
            styles.height = styles.height + 'px';
        }
    }
    return styles;
}

export const visitNode = (node: Node, parent: Node | null, lastVertical: any, indent: string, firstCall?: boolean) => {
    //debugger;
    if(!parent){
        zIndex = 0;
    }
    let content = null;
    let styles: any;
    styles = {};
    let minChildren: Node[] = [];
    const maxChildren: Node[] = [];
    const centerChildren: Node[] = [];
    let bounds = null;
    let nodeBounds = null;

    if (parent != null) {
        nodeBounds = node.absoluteBoundingBox;
        const nx2 = (nodeBounds.x + nodeBounds.width);
        const ny2 = (nodeBounds.y + nodeBounds.height);
        const parentBounds = parent.absoluteBoundingBox;
        const px = parentBounds.x;
        const py = parentBounds.y;

        bounds = {
            left: (nodeBounds.x - px),
            right: px + parentBounds.width - nx2,
            top: lastVertical == null ? nodeBounds.y - py : nodeBounds.y - lastVertical,
            bottom: py + parentBounds.height - ny2,
            width: nodeBounds.width,
            height: nodeBounds.height,
        }
    } else {
        nodeBounds = node.absoluteBoundingBox;
        const nx2 = nodeBounds.x + nodeBounds.width;
        const ny2 = nodeBounds.y + nodeBounds.height;


        bounds = {
            width: nodeBounds.width,
            height: nodeBounds.height,
        }

    }

    expandChildren(node, parent, minChildren, maxChildren, centerChildren, 0);

    let outerClass = 'outerDiv';
    let innerClass = 'innerDiv';
    const cHorizontal = node.constraints && node.constraints.horizontal;
    const cVertical = node.constraints && node.constraints.vertical;
    let outerStyle: any;
    outerStyle = {};

    if (node.order) {
        outerStyle.zIndex = node.order;
    }


    if (node.paddingRight) {
        bounds.width -= node.paddingRight;
    }
    if (node.paddingLeft) {
        bounds.width -= node.paddingLeft;
    }
    if (node.paddingTop) {
        bounds.height -= node.paddingTop;
    }
    if (node.paddingBottom) {
        bounds.height -= node.paddingBottom;
    }

    styles.width = bounds.width;
    styles.height = bounds.height;


    if(node.type === 'RECTANGLE'){
        debugger;
    }


    if (!firstCall || node.type === 'RECTANGLE') {
        styles = positionCalculator(node,bounds, styles, cHorizontal,cVertical);
    }


    switch (node.type) {
        case 'TEXT':
            var lastFill = getPaint(node.fills);
            if (lastFill) {
                styles.color = colorString(lastFill.color);
            }

            var effects = fillEffects(node.effects);
            if(effects.filter.length>1){
                styles.filter = effects.filter;
            }


            var lastStroke = getPaint(node.strokes);
            if (lastStroke) {
                const weight = node.strokeWeight || 1;
                styles.WebkitTextStroke = `${weight}px ${colorString(lastStroke.color)}`;
            }

            var fontStyle = node.style;

            if (fontStyle) {
                styles.fontSize = fontStyle.fontSize;
                styles.fontWeight = fontStyle.fontWeight;
                styles.fontFamily = fontStyle.fontFamily;
                styles.textAlign = fontStyle.textAlignHorizontal;
                styles.fontStyle = fontStyle.italic ? 'italic' : 'normal';
                styles.lineHeight = `${fontStyle.lineHeightPercent * 1.25}%`;
                styles.letterSpacing = `${fontStyle.letterSpacing}px`;
            }
            break;

        case 'VECTOR':
            var colors = fillColor(node.fills);
            var effects = fillEffects(node.effects);
            if(effects.filter.length>1){
                styles.filter = effects.filter;
            }
            if (node.svg && node.svg != 'null' && node.svg != null) {
                node.svg = optimize(node.svg, {
                    plugins: [
                        {
                            name: 'removeAttrs',
                            params: {
                                attrs: '*:(stroke|fill):((?!^none$).)*'
                            }
                        }
                    ]
                }).data;
                styles.fill = colors.hex;
            }

            break;

        default:
            if (node.clipsContent) styles.overflow = 'hidden';
            styles.backgroundColor = fillColor(node.fills).hex
            styles.blendMode = fillColor(node.fills).blendMode;
            styles.backgroundImage = fillColor(node.fills).backgroundImage;


            var effects = fillEffects(node.effects);
            if(effects.filter.length>1){
                styles.filter = effects.filter;
            }

            var lastStroke = getPaint(node.strokes);
            if (lastStroke) {
                const weight = node.strokeWeight || 1;
                styles.WebkitTextStroke = `${weight}px ${colorString(lastStroke.color)}`;
            }

            var fontStyle = node.style;

            if (fontStyle) {
                styles.fontSize = fontStyle.fontSize;
                styles.fontWeight = fontStyle.fontWeight;
                styles.fontFamily = fontStyle.fontFamily;
                styles.textAlign = fontStyle.textAlignHorizontal;
                styles.fontStyle = fontStyle.italic ? 'italic' : 'normal';
                styles.lineHeight = `${fontStyle.lineHeightPercent * 1.25}%`;
                styles.letterSpacing = `${fontStyle.letterSpacing}px`;
            }
            break;
    }
    // if(styles.width && String(styles.width).includes('3.3333')){
    //     debugger;
    // }

    if(node.type === 'RECTANGLE'){
        debugger;
    }


    if(node.blendMode === 'PASS_THROUGH'){
        styles.zIndex = zIndex++;
    }

    node.styles = styles;

    for (const key of SPACE_STYLE_KEYS) {
        if (!node[key]) continue;
        node.styles[key] = node[key];
    }

    if (node[borderKey]) {
        node.styles['borderRadius'] = node[borderKey];
    }

    if (!parent) {
        firstCall = true;
    } else {
        firstCall = false;
    }

    if (node.children) {
        for (const iterator of node.children) {
            visitNode(iterator, node, null, `         `, firstCall);
        }
    }



}

